sup#sh_01:hover:before { content: '민기의 “가만 보면 저 형이 제일 이상하다”는 발언에 대해 \A 산이 “가만 안 봐도 저 형이 제일 이상하다”고 말한 적 있다.'; }
sup#sh_02:hover:before { content: '매일같이 학교에 갈리는 모습을 보며 \A 모두가 팥차를 먹이고 싶어 한다. \A 멤버들의 놀림에 팥차 거부 반응을 보이고 있다.'; }
sup#sh_03:hover:before { content: '주먹밥을 영업하고 다니는 \A 모습을 본 멤버들이 붙인 별명이다.'; }
sup#sh_04:hover:before { content: '무지성 ~게, 에구 등이 있다.'; }
sup#sh_05:hover:before { content: '대표적인 그의 작품은 작이에 걸린 캘린더와 합주 믹싱본이다.'; }

sup#hj_1:hover:before { content: '따봉을 날리는 짤을 자주 써서 그렇다.'; }

sup#yh_01:hover:before { content: '본인이 더 많은 글을 쓰고 싶어 하는 말이다. \A목록에 본인 글만 있으면 민망하다나 뭐라나.'; }
sup#yh_02:hover:before { content: '<정윤호의 별이 빛나는 밤에>를 \A고정 글감으로 사용하며 \A이 글은 노래를 추천해 주는 글이다.'; }
sup#yh_03:hover:before { content: '야노가 취미라는 것을 밝히고 정우영이 붙여 준 별명. \A정윤호가 쓴 ‘네 네 주인님 야노 중이에요……’ 글에 \A‘아 시발 야노윤호야’라고 댓글을 달았다.'; }
sup#yh_04:hover:before { content: '게임만 하면 멍청해져서 붙은 별명이다. \A윤빡이가 되는 경우 주로 에? 라는 \A말버릇을 구사한다.'; }
sup#yh_05:hover:before { content: '그 말이 하루도 안 간다는 것이 \A킬링 포인트이다.'; }

sup#sn_01:hover:before { content: '본인은 이 기믹을 즐기고 있는 눈치다. \A 여친 구한다는 말은 진담 3 할, \A농담 7 할 정도로 볼 수 있다.'; left: -150px !important; }
sup#sn_02:hover:before { content: '방장 산이를 줄인 말. 단톡에서 ㅈㄴㄱㄷ 산이와 이름을 \A구분하기 위해 사용하기 시작했다.'; left: -150px !important; }
sup#sn_03:hover:before { content: '정윤호가 쏘아 올린 공으로, \A과거 개팔 내에서 킹받는 짤꾸가 유행했던 사건이다.'; }
sup#sn_04:hover:before { content: '정작 여친 지원자는 없고 하트만 털고 간다.'; }


sup#mg_01:hover:before { content: '빈도가 잦은 것은 인정하나, \A 사용하는 족족 알아듣는 멤버들이 많다는 점에서 \A "어차피 다 같은 밈친놈들이 아니냐"고 주장한다.'; left: -200px !important; }
sup#mg_02:hover:before { content: '너무 웃겨가 넝수로 오타가 났던 것에서 유래했다.\A 초반에는 민기 자체를 넝수라고 부르는가 싶더니\A 최근에는 민기의 반려 동물을 넝수라고 지칭해 넝수 주인으로\A 불리곤 한다.'; left: -200px !important; }
sup#mg_03:hover:before { content: '그것도 프리뷰 두 장만 저장해도 되냐고 물었다.'; }
sup#mg_04:hover:before { content: '이제는 어사즈를 콘셉트로 즐기고 있다.'; }

sup#wy_01:hover:before { content: '바람 부는 날 본인의 모습이 웃기다며 올렸던 사진은 \A 지금까지도 짤로 사용되며 종종 회자되곤 한다.'; }
sup#wy_02:hover:before { content: '정작 자기를 알아보면 어떻게 아냐고 역정을 낸다.\A(그걸 어떻게 몰라요……)'; }

sup#jh_01:hover:before { content: '상대방이 캐해 해 준 것 짤 쓰기가 마니또 미션이었던 \A 최종호에게 정우영이 햄스터 같다고 한 것이 시초였다.'; left: -150px !important; }
sup#jh_02:hover:before { content: '플랭크 챌린지를 진행했을 때,\A 멤버들에게 운동하라고 닦달하는 모습이 \A 트레이너 같다고 해서 붙여진 별명이다.'; }
sup#jh_03:hover:before { content: '훗날 사실은 재미있어서 튕기다\A 들어온 것이라고 답했다.'; }


