/* 단체 문서 */
#sup_con_01:hover:before { content: '완전체가 된 날짜 기준. \A 첫 결성은 2021 년 10 월 경이다.'; }
#sup_con_02:hover:before { content: '말 그대로 개판으로 모였다.'; }
#sup_con_03:hover:before { content: '본 문서 또한 그 산물이다.'; }
#sup_con_04:hover:before { content: '스파이는 기획자 본인인 강여상이었으며, 나머지 멤버들은 결국 스파이 검거에 실패했다.'; }
#sup_con_05:hover:before { content: '정*호'; }
#sup_con_06:hover:before { content: '이 글에서 박성화의 안 어울리는 직업으로는 ENTP이 나왔다. \A 박성화의 실제 MBTI는 ENTP이 맞다.'; }
#sup_con_07:hover:before { content: '박성화는 정윤호를 보고 따라 하기 시작했다고 했지만, \A 정작 정윤호는 박성화에게서 옮았다고 주장한다.'; }
#sup_con_08:hover:before { content: '보통 이것도 어이없을 때 사용하는 편이다.'; }
#sup_con_09:hover:before { content: '정확하지 않다.'; }
#sup_con_10:hover:before { content: '사실 욕을 하는 특정 인물들만 사용하는 것 \A 같기도 하다.'; }
#sup_con_11:hover:before { content: '처음에는 어색해하던 종호도 이제는 즐기고 있는 것으로 보인다.'; }
#sup_con_12:hover:before { content: '놀랍게도 사실이다. 게임을 하면 이 두 인물이 룰 이해에 있어 가장 미숙한 점을 알 수 있다.'; }
#sup_con_13:hover:before { content: '최산은 그런 정윤호의 댓글을 보고 “투호로 처맞고 싶냐”고 물어봤다.'; }
